import React, { FC, ReactElement, useState } from "react";
import { MdDoubleArrow, MdTransferWithinAStation } from "react-icons/md";
// import { Link } from "react-router-dom";
import { GetTransferForUser__Interface } from "../../actions";
import Button from "../../components/Buttons/Button";
import { APP_URL_FILE } from "../../config/app.config";

type RequestItemProps = {
  transf: GetTransferForUser__Interface;
  expand?: boolean;
  removeTransfer?: (transfer_id: string) => void;
  remove_loading?: boolean;
};

const RequestItem: FC<RequestItemProps> = ({
  transf,
  expand = false,
  remove_loading = false,
  removeTransfer,
}): ReactElement => {
  const [open, setOpen] = useState(false);
  console.log(transf);

  return (
    <div className="p-2 bg-white mb-3 border rounded ">
      <div className="flex">
        <div
          className={`p-3 hidden lg:flex items-center  ${
            transf.requested_status === "APPROVED"
              ? "text-green-700 bg-green-100"
              : "text-accent-800 bg-accent-400"
          } rounded flex items-center`}
        >
          <MdTransferWithinAStation size={80} />
        </div>
        <div className="flex-1 pl-3 pt-1">
          <section
            className="hover:underline cursor-pointer"
            onClick={() => setOpen(!open)}
          >
            <span
              className={`p-1 rounded px-2 py-0.5 text-sm  ${
                transf.requested_status === "APPROVED"
                  ? "bg-green-600 text-white"
                  : "bg-accent-900 text-white"
              }`}
            >
              {transf.requested_status}
            </span>{" "}
            <h2 className="text-lg font-bold">
              {transf.position_code} {transf.position_name}
            </h2>
          </section>

          <div className="pt-1">
            <section className="flex items-center pt-2">
              <section className="border pl-2 hover:bg-gray-200 pr-4 rounded rounded-r-full">
                <h6 className=" font-light text-sm text-yellow-600">
                  Current school
                </h6>
                <h3 className=" font-bold">
                  {transf.school_from_info?.school_code}
                  {" - "}
                  {transf.school_from_info?.school_name}
                </h3>
                <h4 className="text-sm -mt-1 text-gray-500">
                  {transf.school_from_info?.district_name} -{" "}
                  {transf.school_from_info?.sector_name}
                </h4>
              </section>
              <section className="text-gray-500">
                <MdDoubleArrow size={40} />
              </section>
              {transf.requested_school_info?.map((itm, i) => (
                <section
                  key={i}
                  className={`border flex-1 pl-4 rounded hover:bg-gray-200 ${
                    i === 0
                      ? "rounded-l-full rounded-r p-1 mr-2 "
                      : " px-1 rounded mr-2 py-2"
                  }  ${
                    transf.approved_school_id === itm.school_code
                      ? "bg-green-600 text-white hover:bg-green-500"
                      : ""
                  }`}
                >
                  <h6 className=" font-light text-sm text-yellow-600">
                    Requested school {i + 1}
                  </h6>
                  <h4 className=" font-bold">
                    {itm?.school_code}
                    {" - "}
                    {itm?.school_name}
                  </h4>
                  <h4 className="text-sm -mt-1 opacity-70">
                    {itm.district_name} - {itm?.sector_name}
                  </h4>
                </section>
              ))}
            </section>

            <section className="pt-2">
              <label className="text-gray-500 inline-block pr-1">Date: </label>{" "}
              <b>{transf.teacher_requested_transfer_date}</b>
            </section>
          </div>
        </div>
      </div>
      {(open || expand) && (
        <div className="animate__animated  animate__fadeIn animate__faster mt-2 border-t pt-2 grid grid-cols-2">
          {[
            { title: "Transfer type", value: transf.transfer_type },
            { title: "transfer_reason", value: transf.transfer_reason },
            {
              title: "Requested transfer date",
              value: transf.teacher_requested_transfer_date,
            },
            {
              title: "Decision provided on",
              value: transf.incoming_decision_date,
            },
          ].map((itm, i) =>
            !itm.value ? null : (
              <div key={i} className="border px-2 py-1.5  flex items-center">
                <span className="text-gray-500">{itm.title}: </span>{" "}
                <h6 className="font-bold text-black">{itm.value}</h6>
              </div>
            )
          )}
        </div>
      )}
      {transf.teacher_comment && transf.teacher_comment?.length >= 1 && (
        <div className="p-2 my-2 rounded border">
          <span className="text-gray-500">Transfer reason: </span>{" "}
          <h6 className="font-bold text-black">{transf.teacher_comment}</h6>
        </div>
      )}

      {removeTransfer &&
        transf.teacher_transfer_id &&
        transf.requested_status !== "APPROVED" && (
          <div className="pt-4 text-right">
            <Button
              onClick={removeTransfer.bind(this, transf.teacher_transfer_id)}
              theme="danger-light"
              loading={remove_loading}
            >
              Remove transfer request
            </Button>
          </div>
        )}
      {transf.mayor_letter !== null && (
        <div className="p-2 text-right">
          <a
            href={`${APP_URL_FILE}/${transf.mayor_letter}`}
            target="_blank"
            rel="noreferrer"
            download
          >
            <Button theme="accent-light">Download transfer letter</Button>
          </a>
        </div>
      )}
    </div>
  );
};
export default RequestItem;
