import React, { ChangeEvent, FC, ReactElement } from "react";
import { InputSize, InputSizeType } from "./InputTypes";

type TextAreaProps = {
  onChange: (event: ChangeEvent<HTMLTextAreaElement>) => void;
  value: string | number;
  error: string;
  label: string;
  placeholder: string;
  disabled?: boolean;
  name: string;
  className?: string;
  size?: InputSizeType;
  Input_className?: string;
  Label_className?: string;
  InputContainer_className?: string;
  MainContainer_className?: string;
  labelColor?: "text-gray-300" | "text-primary-900 dark:text-primary-100";
};

const TextArea: FC<TextAreaProps> = ({
  onChange,
  value,
  error,
  label,
  placeholder,
  disabled = false,
  name,
  Input_className = "",
  Label_className = "",
  InputContainer_className = "",
  MainContainer_className = "",
  labelColor = "text-primary-900 dark:text-primary-100",
  size = "md",
}): ReactElement => {
  /* function body */
  return (
    <>
      <div className={`w-full my-2 ${MainContainer_className}`}>
        {label !== "" && (
          <label
            htmlFor="any-form"
            className={`${labelColor} ${Label_className}`}
          >
            {label}
          </label>
        )}
        <div className={`rounded relative ${InputContainer_className}`}>
          <textarea
            name={name}
            placeholder={placeholder}
            onChange={onChange}
            className={`
                w-full text-lg rounded border-2 
                ${InputSize(size)}
                ${
                  error !== ""
                    ? "border-red-500 dark:text-white bg-red-100 dark:bg-red-900 dark:bg-opacity-30"
                    : "border-primary-800 dark:text-white bg-white dark:bg-primary-900 "
                }
                px-3 ${Input_className}`}
            value={value}
            disabled={disabled}
          />

          {error !== "" && (
            <p className="text-sm text-red-600 dark:text-red-500">{error}</p>
          )}
        </div>
      </div>
    </>
  );
};
export default TextArea;
