import React from "react";
import { connect } from "react-redux";
import {
  Auth,
  FC_GetOutGoingTransfers,
  FC_SendTransfer,
  GetTransferForUser__Interface,
} from "../../actions";
import Button from "../../components/Buttons/Button";
import DashboardContainer from "../../components/DashboardContainer/DashboardContainer";
import Heading from "../../components/Heading/Heading";
import TextArea from "../../components/Inputs/TextArea";
import Loading from "../../components/Loading/Loading";
import Modal, { ModalSize, Themes } from "../../components/Modal/Modal";
import { StoreState } from "../../reducers";
import TransferItem from "../RequestTransfer/TransferItem";

// props for the component
interface AppProps {
  auth: Auth;
}

interface AppState {
  loading: boolean;
  error: {
    target: "main-error" | "main-success" | null | "request-input";
    msg: string;
  };
  limit: number;
  page: number;
  data: GetTransferForUser__Interface[];
  total: number;
  next: number;
  previous: number;
  selected_transfer_id: string | null;
  transfer_issue: string;
  loading_request: boolean;
}

const errorDefault = {
  target: null,
  msg: "",
};

class _OutgoingTransfers extends React.Component<AppProps, AppState> {
  constructor(props: AppProps) {
    super(props);

    this.state = {
      loading: false,
      error: errorDefault,
      data: [],
      limit: 14,
      page: 1,
      total: 0,
      next: 0,
      previous: 0,
      selected_transfer_id: null,
      transfer_issue: "",
      loading_request: false,
    };
  }

  componentDidMount() {
    if (!this.props.auth.district || !this.props.auth.district.district_code) {
      return this.setState({
        error: {
          target: "main-error",
          msg: "There is no district assigned to you!",
        },
      });
    }

    this.loadingData(this.props.auth.district.district_code, this.state.page);
  }

  loadingData = (district: string, page: number) => {
    this.setState({
      loading: true,
      selected_transfer_id: null,
      data: [],
      transfer_issue: "",
    });
    FC_GetOutGoingTransfers(
      {
        district_code: district,
        limit: this.state.limit,
        page: page,
      },
      (status, data, msg) => {
        this.setState({
          loading: false,
          data: data?.district_transfer_request
            ? data?.district_transfer_request
            : [],
          total: data?.total_transfer ? data.total_transfer : 0,
          next: data?.next ? +data?.next : 0,
          previous: data?.previous ? +data?.previous : 0,
          error: status
            ? {
                target: "main-error",
                msg: msg,
              }
            : errorDefault,
          page: page,
        });
        console.log({ status, data, msg });
      }
    );
  };

  paginateData = (page: number) => {
    if (this.props.auth.district?.district_code)
      this.loadingData(this.props.auth.district.district_code, page);
  };

  submitIssue = () => {
    if (this.state.transfer_issue.length <= 20)
      return this.setState({
        error: {
          target: "request-input",
          msg: "Your message is too short, minimum characters are 20",
        },
      });

    if (
      this.state.selected_transfer_id === "" ||
      this.state.selected_transfer_id === null
    )
      return this.setState({
        error: {
          target: "request-input",
          msg: "Your message is too short, minimum characters are 20",
        },
      });

    this.setState({
      loading_request: true,
    });

    FC_SendTransfer(
      {
        teacher_transfer_id: this.state.selected_transfer_id,
        transfer_issue_comment: this.state.transfer_issue,
      },
      (status, data, msg) => {
        this.setState({
          loading_request: false,
          error:
            status === true
              ? {
                  target: "main-success",
                  msg: "The request has been submitted successfully.",
                }
              : {
                  target: "main-error",
                  msg: msg,
                },
        });
      }
    );
  };

  render() {
    let total_pages = (this.state.total / this.state.limit).toFixed(0);
    let foundTransfer = this.state.data.find(
      (transf) => transf.teacher_transfer_id === this.state.selected_transfer_id
    );
    return (
      <>
        {this.state.selected_transfer_id !== null &&
          foundTransfer !== undefined && (
            <Modal
              backDrop={true}
              theme={Themes.default}
              close={() =>
                this.setState({
                  selected_transfer_id: null,
                  transfer_issue: "",
                })
              }
              backDropClose={false}
              widthSizeClass={ModalSize.extraLarge}
              displayClose={true}
              title="Transfer details"
              padding={{
                title: true,
                body: false,
                footer: undefined,
              }}
            >
              <div className="border-t">
                <div className="p-4 text-gray-500">
                  <section>
                    Transfer id:{" "}
                    <b className="text-black">
                      {foundTransfer.teacher_transfer_id}
                    </b>
                  </section>
                  <section>
                    Teacher names:{" "}
                    <b className="text-black">
                      {foundTransfer.teacher_full_name}
                    </b>
                  </section>
                  <section>
                    Teacher's Phone number:{" "}
                    <b className="text-black">
                      {foundTransfer.teacher_phone_number}
                    </b>
                  </section>
                </div>
                <TransferItem expand={true} transf={foundTransfer} />
                <div className="pt-3 px-2 bg-gray-100 m-2">
                  <TextArea
                    onChange={(e) =>
                      this.setState({ transfer_issue: e.target.value })
                    }
                    disabled={this.state.loading_request}
                    value={this.state.transfer_issue}
                    error={
                      this.state.error.target === "request-input"
                        ? this.state.error.msg
                        : ""
                    }
                    label={"Why do you want this transfer to be Canceled?"}
                    placeholder={"Type the reason here"}
                    name={""}
                  ></TextArea>

                  <div className="pt-1">
                    <Button
                      onClick={this.submitIssue}
                      loading={this.state.loading_request}
                    >
                      Submit request
                    </Button>
                  </div>
                </div>
              </div>
            </Modal>
          )}
        <div className="container mx-auto p-2 flex items-center">
          <Heading className="flex-1">Outgoing transfers</Heading>
          <section className="text-center p-1 bg-accent-800 bg-opacity-20 text-accent-900 px-4 rounded">
            <span className="text-sm block text-gray-500 -mb-1">Total:</span>
            <h3 className="font-bold text-xl">{this.state.total}</h3>
          </section>
        </div>
        <DashboardContainer transparent>
          <table className="w-full">
            <thead className="text-left bg-white">
              <tr className="bg-accent-900 bg-opacity-10">
                <th className="p-2 border-gray-300 border">#</th>
                <th className="p-2 border-gray-300 border">Names</th>
                <th className="p-2 border-gray-300 border">School Code</th>
                <th className="p-2 border-gray-300 border">School name</th>
                <th className="p-2 border-gray-300 border">Position code</th>
                <th className="p-2 border-gray-300 border">Position name</th>
                <th className="p-2 border-gray-300 border">Qualification</th>
              </tr>
            </thead>
            <tbody>
              {this.state.loading === true ? (
                <tr className=" ">
                  <td colSpan={8} className="py-4 text-center border">
                    <Loading />
                  </td>
                </tr>
              ) : this.state.data.length <= 0 ? (
                <tr>
                  <td colSpan={8} className="py-4 text-center border">
                    <h1 className="text-2xl text-gray-400">No data found</h1>
                    {/* <p className="text-gray"></p> */}
                  </td>
                </tr>
              ) : (
                this.state.data.map((itm, i) => (
                  <tr
                    key={i}
                    className="bg-white cursor-pointer hover:bg-yellow-400 hover:bg-opacity-30"
                    onClick={() =>
                      this.setState({
                        selected_transfer_id: itm.teacher_transfer_id,
                      })
                    }
                  >
                    <td className="border p-2">{i + 1}</td>
                    <td className="p-2 border">{itm.teacher_full_name}</td>
                    <td className="p-2 border">
                      {itm.school_from_info?.school_code}
                    </td>
                    <td className="p-2 border">
                      {itm.school_from_info?.school_name}
                    </td>
                    <td className="p-2 border">{itm.position_code}</td>
                    <td className="p-2 border">{itm.position_name}</td>
                    <td className="p-2 border">{itm.qualification_name}</td>
                  </tr>
                ))
              )}
            </tbody>
          </table>

          <div className="flex gap-3 mx-auto pt-3">
            {Array(+total_pages)
              .fill("0")
              .map((e, i) => (
                <button
                  className={`p-2 py-1 ${
                    +this.state.page === i + 1
                      ? "bg-accent-800 text-black font-bold"
                      : "bg-opacity-25"
                  } rounded-full bg-accent-800 `}
                  onClick={this.paginateData.bind(this, i + 1)}
                >
                  {i + 1}
                </button>
              ))}
          </div>
        </DashboardContainer>
      </>
    );
  }
}

const mapStateToProps = ({ auth }: StoreState): { auth: Auth } => {
  return { auth: auth };
};

export const OutgoingTransfers = connect(
  mapStateToProps,
  {}
)(_OutgoingTransfers);
